nav.site-header__nav--archive{width:100vw;max-width:100vw;padding-left:24px}
.archive{margin:0;padding:0;font-size:12px;letter-spacing:.5px;background:#17191c}
.archive p{font-size:1.25em}
.archive a{color:#c88064}
.archive h1{font-size:3em}
.archive h2{font-size:1.5em}
.archive a:hover{color:#9f4726;-webkit-transition:.4s;transition:.4s}
.archive a:visited{color:#c88064}
.archive hr{height:1px;margin:25px 0;background-image:-webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(255, 255, 255, 0.75)), to(rgba(0, 0, 0, 0)));background-image:linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));border:0}
.archive sup{margin:0 .2em}
.archive #csis{top:0;width:100%;padding:10px 0;text-align:center;background:#fff}
.archive .header{position:relative;display:inline-block;width:100%;height:100%;margin-top:-96px;padding-top:120px;text-align:center;background-image:linear-gradient(to bottom, #17191c 0, #17191c 96px, rgba(31, 34, 38, 0.62) 96px, rgba(31, 34, 38, 0.62) 50%, rgba(31, 34, 38, 0) 75%),url("https://res.cloudinary.com/csisideaslab/image/upload/v1561580632/india-reforms/archive_hero.jpg");background-position-x:75%,75%;background-size:cover,cover}
.archive .overlay{position:relative;background-color:rgba(255,255,255,.7)}
.archive .header h1{position:relative;margin-top:0;padding:10%;color:#fff;line-height:1.25}
.archive .header h1 span{display:block;text-transform:uppercase}
.archive .header a{position:relative;bottom:100px;display:inline-block;margin-top:2em;padding:15px;color:#fff;font-weight:bold;font-size:1.8em;text-transform:uppercase;text-decoration:none;border:solid 2px #fff}
@media screen and (max-width: 990px){.archive .header a{bottom:50px}}
@media screen and (max-width: 510px){.archive .header a{display:none}}
.archive .header a:hover{color:#9f4726;background:#fff;border-color:#3f4a96;-webkit-transition:.4s;transition:.4s}
.archive .overlay::before{position:absolute;top:0;left:0;z-index:0;display:block;width:100%;height:100%;background-color:rgba(0,0,0,.5);content:" "}
.archive .article *{position:relative}
.archive p#cutline{color:#fff;font-size:1em;font-style:italic;text-align:center}
.archive .grid-item{width:25%}
.archive .grid-item--width2{width:50%}
.archive .portfolioFilter{width:100%;margin:auto auto 1%;text-align:center}
.archive .portfolioFilter a{margin:0 5px;text-transform:uppercase}
.archive #site-description{float:none;max-width:800px;margin:50px auto;color:#fff;font-size:1.6em}
@media screen and (max-width: 760px){.archive #site-description p{font-size:1.4em}}
.archive #center-desc{margin:10px 5%;color:#fff;text-align:center}
.archive .contenthover{padding:20px 20px 10px}
.archive .contenthover,.archive .contenthover h3,.archive contenthover a{color:#fff}
.archive .contenthover h3,.archive .contenthover p{margin:0 0 10px;padding:0;line-height:1.4em}
.archive .contenthover a.mybutton{display:block;float:left;padding:5px 10px;color:#fff;background:#3c9632;border-radius:4px}
.archive .contenthover a.mybutton:hover{background:#34742d}
.archive #page-container{width:100%;max-width:960px;margin:0 auto;text-align:center}
.archive .color_key{position:relative;width:250px;height:40px;margin:20px 5px 25px 17%;font-weight:bold;text-align:center}
@media screen and (max-width: 590px){.archive .color_key{margin:20px 5px 25px -10%;font-size:1em}}
.archive .color_key ul{width:450px;margin:0;padding:0;list-style-type:none}
.archive .color_key ul li{color:#fff}
.archive .color_key ul li p{float:left;margin:-1px 5px 0;font-size:1.2em}
.archive #blue_key{float:left;width:12px;height:12px;margin-right:10px;background-color:#3f4a96}
.archive #red_key{float:left;width:12px;height:12px;margin-right:10px;background-color:#9f4726}
.archive #gray_key{float:left;width:12px;height:12px;margin-right:10px;background-color:#69696c}
.archive .policycontainer{width:100%;max-width:960px;margin:10px 2% 50px;text-align:center}
.archive .indepth{margin:15px 5%}
.archive a#social-links{opacity:1}
.archive a#social-links:hover{opacity:.6}
.archive .portfolioFilter{display:block;width:65%;max-width:960px}
.archive .portfolioFilter h2{color:#fff}
.archive .button{display:inline-block;margin-bottom:10px;padding:6px;color:#fff;font-size:1.4em;letter-spacing:.6px;text-align:center;text-transform:uppercase;background:#60666a;border:none;cursor:pointer}
.archive .button:hover{color:#fff;background-color:#c88064}
.archive .button:active,.archive .button.is-checked{background-color:#9f4726}
.archive .button.is-checked{color:#fff}
.archive .button:active{-webkit-box-shadow:inset 0 1px 10px rgba(0,0,0,.8);box-shadow:inset 0 1px 10px rgba(0,0,0,.8)}
.archive .button-group::after{display:block;clear:both;content:""}
.archive .button-group .button{margin-right:1px;margin-left:0;border-radius:0}
.archive .policy{width:295px;height:325px;margin:10px;overflow:hidden;color:#e3e3e3;text-align:center;background:#393939;border-radius:10px}
.archive .policy div{top:0;left:0;width:295px;height:325px;text-align:center}
.archive .policy div.first{z-index:1000}
.archive .policy:hover div.first{display:none}
.archive .info-text{padding-top:25px;color:#fff;background:#45484d;background:radial-gradient(ellipse at center, #45484d 0%, #000 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#45484d", endColorstr="#000000", GradientType=1)}
.archive .info-text p{margin:15px}
@media screen and (max-width: 990px){.archive .policy{width:275px;height:375px}.archive .policy h2{font-size:1.6em}.archive .policy div{width:275px;height:375px}.archive .policy .info-text{width:275px;height:375px}}
@media screen and (max-width: 920px){.archive .policy{width:265px;height:265px}.archive .policy div{width:265px;height:265px}.archive .policy .info-text{width:265px;height:265px}}
@media screen and (max-width: 890px){.archive .policy{width:255px;height:375px}.archive .policy div{width:255px;height:375px}.archive .policy .info-text{width:255px;height:375px}.archive .policy h2{font-size:1.6em}.archive .policy p{font-size:1.2em}}
@media screen and (max-width: 860px){.archive .policy{width:240px;height:375px}.archive .policy div{width:240px;height:375px}.archive .policy .info-text{width:240px;height:375px}}
.archive #policy-align{margin:auto;text-align:center}
.archive h2.name{margin:0 0 10px;padding:10px;color:#f2f2f2;font-weight:bold;border-radius:10px 10px 0 0}
.archive .blue{background:#3f4a96}
.archive .red{background:#9f4726}
.archive .gray{background:#69696c}
.archive .status{margin:20px 0 10px;padding:0;font-weight:bold;text-transform:uppercase}
.archive .difficulty{margin:0;padding:0;font-weight:bold;text-align:center;text-transform:uppercase}
.archive .desc{margin:0;padding:15px;font-size:14px}
.archive .source{margin:0;padding:0 5px;font-size:12px;text-align:left}
.archive #method{margin:0 20px;color:#fff}
.archive #footer{width:100%;max-width:800px;margin:10px auto 25px}
.archive #copyright{position:relative;bottom:0;float:left;width:100%;height:135px;background:#222}
.archive #copyright p{margin:28px auto;color:#fff;text-align:center}
